@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
*{
  font-family: 'Poppins', sans-serif;
}

html {
  scroll-behavior: smooth;
}

body{
  margin:0;
  padding:0;
}
.navbar{
    height: 5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding:1rem 4rem;
    position: -webkit-sticky;
    position: sticky;
    top:0;
}

.active{
    background-color:#2d2d2dea ;
    color:#FFF !important;
    -webkit-backdrop-filter: saturate(180%) blur(3px);
            backdrop-filter: saturate(180%) blur(3px);
}

.navbar__right{
    display: flex;
    justify-content: center;
    align-items: center;
}

.tabs{
    padding: 0;
    padding-left: 2rem;
}

.tabs__num{   
    position: absolute;
    margin-top: -1rem;
    font-size: 0.7rem;
}

.tabs__main{
    margin: 0;
    text-decoration: none;
    color:black;    
}

.col{
    color:#FFF;
    text-decoration: none;
}

.col:hover{
    opacity: 0.8;
}

.tabs__main:hover{
    color: #192b53;
    font-weight: bold;
}

.about{
    background-color: #090909;
    width: 100%;
    height: 600px;
    display: flex;
    align-items: center;
    justify-content: center;       
}

.about__main{
    display: flex;
    justify-content: space-around;    
    width: 100vw;
    color:#FFF; 
}

.about__wrapper{
    display: flex;
    flex-direction: column;
    width: 300px;
}

.main__title{
    color:#FFF; 
}


.main__about{
    color:#FFF; 
    display: flex;
    align-items: center;
}

.gmail{
    padding-right: 0.5rem;
}


.about__imgwrap{
    width: 221px;
    height: 221px;
    box-shadow: -10px 10px #2D2D2D;
}


.contact__main{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2rem 0;
}

.contact__mail{
    display: flex;
    flex-direction: column;
    padding: 0 2rem;
}

.mail{
    margin: 0;
}

.distrub{
    margin: 0;;
    color:#9CA3AF;
}

.red{
    color:#FF2358;
}
.hometop{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    width: 100%;
    height: 100vh; 
    flex-wrap: wrap;   
}

.text__links{
    display: flex;
    justify-content: flex-start;
    cursor: pointer;
}

.text_first{
    margin:0;
    color: #111827;
    font-size: 24px;
    font-style: italic;
}

.text_scnd{
    margin:0;
    font-size: 64px;
    color: #111827;
    font-weight: bold;
}

.hometop__wrapper{
    display: flex;
    justify-content: center;
    align-items: center;  
    flex-wrap: wrap;  
}

.wrapper__img{
    width: 421px;
    height: 421px;
}

.img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.link{
    padding-right: 2rem;
    text-decoration: underline;
    color:#111827;
}

.link:hover{
    color: #192b53;
    font-weight: bold;
}

.jobname{
    text-decoration: none;
    color: #111827;
}

.jobname:hover{
    color: #192b53;
    font-weight: bold;
}

.scroll{
    display: flex;
    position: absolute;
    align-items:center;
    -ms-writing-mode: tb-rl;
        writing-mode: vertical-rl;
    -webkit-text-orientation: mixed;
            text-orientation: mixed;
    justify-content:center;    
    left: 3rem;
    bottom: 0;
    transform: rotate(180deg);
    z-index: -1;
}

.scroll__per{
    margin-bottom: 0.5rem;
    font-weight: bold;
    width: 2px;
    height: 2rem;
    background: #9CA3AF;
}


.work{
    width: 100%;
    padding:5rem 0;
    display: flex;
    align-items: center;
    justify-content: center;    
}

.work__main{
    display: flex;
    justify-content: space-evenly;    
    width: 100vw;   
    flex-wrap: wrap;
}

.work__wrapper{
    display: flex;
    justify-content: space-around;
    flex-direction: column;
}

.work__single{
    display: flex;
    justify-content: center;    
    justify-content: space-evenly;
    padding:0.2rem 0;
}

.work__single:nth-of-type(2n){
    flex-direction: row-reverse;
}

.single__wrapper{
    height: 240px;
    width: 33.33333%;
}

.single__about{
    height: 240px;
    width: 67%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #090909 ;
}

.work__title{
    color:#111827;
}

.about__title{
    color:#F9FAFB;
    margin:0;
}

.about__about{
    color:#6C6C6C;
}

.linkicon{
    color:#FFF;
}

.link{
    display: flex;  
    z-index: 1;  
}

.href{
    font-size: 1.5rem;
    text-align: center;
    margin:0;
    margin-left: 2rem;
    padding:0;
    z-index: 1;
}

.linkicon:hover{
    color: #6C6C6C;
}


@media only screen and (max-width: 600px) {
    
    .navbar{
        padding:1rem;
    }

    .hometop{
        text-align: center;
    }

    .about__main{
        flex-wrap: wrap;
        flex-direction: column;
        align-items: center;
    }

    .text__links{
        justify-content: space-evenly;
    }

    .scroll{
        left:0;
        bottom: -6rem;;
    }

    .work{
        padding: 0rem;
        padding-top: 2rem;
    }

    .about__wrapper{
        text-align: center;
    }    

    .work__single{
        flex-direction: column-reverse;
        padding:0rem;
    }

    .single__wrapper{
        height: 140px;
        width: 100vw;
    }

    .single__about{
        height: 140px;
        width: 100vw;
    }
    .work__single:nth-of-type(2n) {
        flex-direction: column-reverse;
    }

    .work__main{
        padding: 0 0rem;
        flex-direction: column;
        align-items: center;
    }

    .link{
        padding:0;
    }

    .wrapper__img{
        width: 321px;
        height: 321px;
    }

    .about__title{
        font-size: 0.9rem;
    }

    .main__about{
        justify-content: center;
    }
  }
